.semi-transparent-bg {
  padding: 20px;
  background: rgba(0, 0, 0, 0.45);
  border-radius: 10px;
  transition: background-color .3s ease-in-out; }
  .semi-transparent-bg_hover:hover {
    background: black; }

.aspect-ratio-wrapper {
  padding-top: 56.25%; }

.project-slide {
  position: relative;
  overflow: hidden;
  background: #000; }
  .project-slide__background {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: no-repeat top;
    background-size: cover; }
  .project-slide__content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 95%;
    max-width: 35em;
    height: 80%;
    display: flex;
    flex-direction: column;
    color: #fff; }
  .project-slide__title {
    align-self: center;
    margin-bottom: auto;
    font-size: 1.5em;
    text-align: center; }
    .project-slide__title_viewed {
      background: green; }
  .project-slide__description {
    width: 50%;
    align-self: flex-start;
    transform: translateX(-1em);
    opacity: 0;
    transition: opacity .75s ease-out, transform .75s ease-out; }
    .project-slide__description_viewed {
      transform: translateX(0);
      opacity: 1; }
  .project-slide__stack {
    width: 50%;
    align-self: flex-end;
    transform: translateX(1em);
    opacity: 0;
    transition: opacity .75s ease-out, transform .75s ease-out; }
    .project-slide__stack_viewed {
      transform: translateX(0);
      opacity: 1; }
  .project-slide__link {
    display: inline-block;
    align-self: center;
    margin-top: auto;
    text-transform: uppercase;
    font-weight: 500; }
  .project-slide a {
    color: #fff;
    text-decoration: none; }

@media (max-width: 1024px) {
  .aspect-ratio-wrapper {
    padding-top: 0;
    min-height: 35em; } }
