.toggle-button {
  position: relative;
  margin: 0.5rem; }
  .toggle-button__title {
    text-align: center;
    font-weight: bold; }
  .toggle-button__input {
    position: absolute;
    margin-left: -9999px;
    visibility: hidden; }
    .toggle-button__input:checked + .toggle-button__label {
      background-color: #4CD964; }
    .toggle-button__input:checked + .toggle-button__label:before {
      left: 32px; }
  .toggle-button__label {
    width: 54px;
    height: 24px;
    display: inline-block;
    position: relative;
    border-radius: 50px;
    overflow: hidden;
    outline: none;
    border: none;
    cursor: pointer;
    background-color: #777777;
    transition: background-color ease 0.3s; }
    .toggle-button__label::before {
      content: " ";
      display: block;
      position: absolute;
      z-index: 2;
      width: 20px;
      height: 20px;
      background: #fff;
      left: 2px;
      top: 2px;
      border-radius: 50%;
      font: 10px/20px Helvetica;
      text-transform: uppercase;
      font-weight: bold;
      text-indent: -22px;
      word-spacing: 28px;
      color: #fff;
      text-shadow: -1px -1px rgba(0, 0, 0, 0.15);
      white-space: nowrap;
      box-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
      transition: all cubic-bezier(0.3, 1.5, 0.7, 1) 0.3s; }
