.pomodoro-clock-wrapper {
  background: #C14242;
  user-select: none; }

.pomodoro-clock {
  width: 500px;
  min-width: 250px;
  font-size: 30px; }

.pomodoro-clock {
  margin: auto;
  color: white;
  text-align: center;
  text-transform: capitalize;
  background: transparent; }
  .pomodoro-clock__label {
    font-size: 1.5em;
    font-weight: bold;
    padding-bottom: 0.5em; }
  .pomodoro-clock__setting {
    display: inline-block; }
    .pomodoro-clock__setting span {
      display: inline-block;
      width: 1.4em; }
  .pomodoro-clock__timer {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    width: 13em;
    height: 13em;
    margin: 0.5em auto; }
    .pomodoro-clock__timer__digital {
      margin: 0 auto; }
    .pomodoro-clock__timer__circle {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      margin: auto; }
    .pomodoro-clock__timer .timer__label {
      font-size: 1.5em; }
    .pomodoro-clock__timer .timer__time-left {
      font-family: 'Share Tech Mono', monospace;
      font-size: 3em; }
  .pomodoro-clock__control {
    flex-grow: 0; }
  .pomodoro-clock__button {
    display: inline-block;
    position: relative;
    height: 1em;
    font-size: 1em;
    line-height: 1;
    color: white;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 2; }
    .pomodoro-clock__button_reset {
      margin: 0 0.5em;
      font-size: 1.5em;
      vertical-align: 30%; }
    .pomodoro-clock__button_start-pause {
      font-size: 2em; }

@media (max-width: 400px) {
  .pomodoro-clock {
    width: 350px;
    min-width: 175px;
    font-size: 21px; } }
