.scrolless {
  display: flex;
  align-items: center;
  width: 100%;
  height: 100%; }
  .scrolless__track {
    display: flex;
    width: 100%;
    height: 100%;
    overflow: hidden; }
  .scrolless__inner {
    width: 100%;
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    user-select: none; }
  .scrolless__btn {
    line-height: 0; }
  .scrolless__arrow {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    visibility: visible;
    cursor: pointer; }
    .scrolless__arrow::before {
      content: '';
      width: 10px;
      height: 10px;
      position: absolute;
      top: 50%;
      left: 50%;
      transform-origin: 0 0;
      transform: rotate(45deg) translate(-37.5%, -62.5%);
      border-left: 1px solid red;
      border-bottom: 1px solid red; }
    .scrolless__arrow_left {
      transform: rotate(0deg); }
    .scrolless__arrow_right {
      transform: rotate(180deg); }
    .scrolless__arrow_hidden {
      visibility: hidden; }
