.project-wrapper {
  position: relative;
  width: 100%;
  max-width: 1600px;
  min-width: 320px;
  min-height: calc(100vh - 20px);
  margin: auto;
  padding: 10px 10px;
  flex-basis: 100%;
  display: flex;
  align-items: center;
  overflow: auto;
  z-index: 100;
  user-select: none; }
  .project-wrapper__back-container {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    width: 100%;
    max-width: 1600px;
    min-width: 320px;
    height: 4rem;
    margin: 0 auto; }
  .project-wrapper__back {
    position: absolute;
    right: 0;
    top: 0;
    line-height: 0;
    cursor: pointer; }
    .project-wrapper__back::after {
      content: "\00d7";
      display: inline-block;
      width: 4rem;
      height: 4rem;
      font-size: 4rem;
      text-align: center;
      line-height: 1;
      color: #000; }
