.carousel-wrapper {
  position: relative;
  width: 100%;
  max-width: 960px;
  margin: auto;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.45);
  touch-action: pan-y;
  user-select: none;
  -webkit-tap-highlight-color: transparent;
  z-index: 99; }
  .carousel-wrapper .arrow {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    z-index: 50; }
    .carousel-wrapper .arrow.slide-prev {
      left: 3%; }
    .carousel-wrapper .arrow.slide-next {
      right: 3%; }
    .carousel-wrapper .arrow .icon-arrow {
      position: relative;
      display: inline-block;
      width: 40px;
      height: 40px;
      background: #fff;
      border-radius: 50%;
      opacity: 0.7;
      transition: opacity .3s ease-in-out;
      cursor: pointer;
      backface-visibility: hidden;
      transform: translateZ(0);
      filter: blur(0); }
      .carousel-wrapper .arrow .icon-arrow::before {
        content: '';
        width: 10px;
        height: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform-origin: 0 0;
        transform: rotate(45deg) translate(-37.5%, -62.5%);
        border-left: 1px solid black;
        border-bottom: 1px solid black;
        backface-visibility: hidden;
        filter: blur(0); }
      .hasHover .carousel-wrapper .arrow .icon-arrow:hover {
        opacity: 1; }
  .carousel-wrapper .slider {
    overflow: hidden; }
    .carousel-wrapper .slider .track::after {
      content: '';
      display: table;
      clear: both; }
    .carousel-wrapper .slider .track .slide {
      float: left; }
  .carousel-wrapper .dots {
    position: absolute;
    bottom: 3%;
    left: 0;
    right: 0;
    text-align: center; }
    .carousel-wrapper .dots__dot {
      display: inline-block;
      width: 15px;
      height: 15px;
      margin: 0 3px;
      background: #fff;
      outline: none;
      border-radius: 50%;
      cursor: pointer;
      opacity: 0.7;
      transition: opacity .3s ease-in-out; }
      .carousel-wrapper .dots__dot_active,
      .hasHover .carousel-wrapper .dots__dot:hover {
        opacity: 1; }

@media (max-width: 1024px) {
  .carousel-wrapper .arrow {
    display: none; }
  .carousel-wrapper .dots__dot {
    width: 25px;
    height: 25px;
    margin: 0 5px; } }
