.welcome {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100vh;
  text-align: center;
  font-size: 2em;
  background: transparent; }
  .welcome__content {
    max-width: 98%; }
  .welcome__scroll-container {
    position: absolute;
    bottom: 3%;
    left: 0;
    right: 0;
    margin: 0 auto; }
  .welcome__scroll-icon {
    opacity: 0.7;
    transition: opacity .3s ease-in-out;
    cursor: pointer; }
    .hasHover .welcome__scroll-icon:hover {
      opacity: 1; }
  .welcome__links {
    margin-top: 10px; }
    .welcome__links > a {
      display: inline-block;
      width: 40px;
      height: 40px;
      padding: 6px;
      margin: 0 10px;
      color: #fff;
      line-height: 1;
      text-decoration: none;
      background: #000;
      border-radius: 50%; }
      .hasHover .welcome__links > a:hover {
        opacity: 0.7; }
    .welcome__links__icon {
      vertical-align: inherit; }
