.weather-app-wrapper {
  color: #000;
  background: #d3d3d3; }

.weather-app {
  margin: auto;
  width: 100%;
  max-width: 480px;
  min-width: 320px;
  min-height: 300px;
  font-size: 20px;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
  text-align: center; }
  .weather-app__header {
    font-size: 50px;
    margin-bottom: 10px; }
  .weather-app .weather {
    font-size: 1.2em;
    line-height: 1; }
    .weather-app .weather__address {
      margin-bottom: 10px;
      line-height: 1.5; }
    .weather-app .weather__icon {
      display: inline-block;
      margin-right: 10px;
      font-size: 40px;
      vertical-align: 20%;
      line-height: normal; }
    .weather-app .weather__temp {
      display: inline-block;
      font-family: 'Droid Sans Mono', monospace;
      font-size: 80px; }
    .weather-app .weather__degree {
      display: inline-block;
      font-size: 80px;
      font-weight: 100;
      line-height: 1; }
    .weather-app .weather__tempUnit {
      display: inline-block;
      margin-left: 0;
      font-size: 36px;
      font-weight: 400;
      font-family: 'Share Tech Mono', monospace;
      color: red;
      vertical-align: 90%;
      line-height: 1;
      cursor: pointer; }
    .weather-app .weather__description {
      margin-top: 10px;
      line-height: 1.5;
      text-transform: capitalize; }
  .weather-app__search {
    margin: 20px auto; }
  .weather-app .search-box {
    display: flex;
    width: 100%;
    max-width: 320px;
    line-height: 2;
    background: #fff;
    border-radius: 2px; }
    .weather-app .search-box__input {
      width: 100%;
      padding: 2px 6px; }
      .weather-app .search-box__input > input {
        display: inline-block;
        width: 100%;
        height: 100%;
        padding-left: 4px;
        vertical-align: top;
        font-size: 20px;
        background: transparent;
        border: none;
        outline: none; }
        .weather-app .search-box__input > input::-ms-clear {
          display: none; }
    .weather-app .search-box__btn {
      display: inline-block;
      flex-shrink: 0;
      flex-grow: 0;
      width: 44px;
      padding: 2px 6px;
      opacity: 0.5;
      background: transparent;
      border: none;
      cursor: pointer; }
      .hasHover .weather-app .search-box__btn:hover {
        opacity: 1; }
