.calc-wrapper {
  background-color: #306630; }

#calculator {
  width: 304px;
  margin: auto;
  flex-shrink: 0;
  padding: 2px;
  position: relative;
  background: #000; }

#display {
  line-height: 1.5;
  font-size: 1.6em;
  text-align: right;
  color: #FFF; }

#formula {
  height: 20px;
  min-height: 20px;
  overflow-wrap: break-word;
  text-align: right;
  color: #FF0; }

.calcButton {
  width: 75px;
  height: 75px;
  position: relative;
  border: none;
  outline: 1px solid #000;
  font-size: 1.2em;
  text-align: center;
  color: #FFF;
  background: #555; }
  .hasHover .calcButton:hover {
    color: #FF0;
    outline: 1.5px solid #FF0;
    z-index: 1; }

.zero {
  width: 150px; }

.clear {
  width: 150px;
  background: #900; }

.signs, .equals, .operator {
  font-size: 1.7em;
  vertical-align: bottom; }

.equals {
  height: 150px;
  position: absolute;
  bottom: 2px;
  right: 2px;
  background: #009; }

.operator {
  background: #999; }
